h1,
h2 {
	text-align: center;
	text-shadow: 0px 0px 5px #eee;
	line-height: 5rem;
}

section.nosotros {
	width: 50%;
	min-width: 300px;
	min-height: 60vh;
	background-color: #eee;
	margin: auto;
	border-radius: 7px;
	box-shadow: 0 0 10px #222;
	padding: 50px;
	padding-top: 0%;
	margin-bottom: 40px;
}
section.nosotros li {
	padding: 5px;
	margin-left: 13px;
}
