div.contacto h1 {
	text-align: center;
	text-shadow: 0px 0px 5px #eee;
	line-height: 5rem;
}

div.contacto h1 + div {
	width: 50%;
	min-width: 300px;
	min-height: 60vh;
	background-color: #eee;
	margin: auto;
	border-radius: 7px;
	box-shadow: 0 0 10px #222;
	padding: 20px;
}
div.contacto h1 + div label {
	display: block;
	line-height: 2rem;
	border-radius: 5px;
	width: -moz-min-content;
	width: min-content;
	padding: 0 15px;
	font-weight: bold;
}
div.contacto h1 + div label::first-letter {
	text-transform: capitalize;
}
div.contacto h1 + div input {
	width: 100%;
	margin-top: 7px;
	margin-bottom: 20px;
	line-height: 2rem;
	outline: none;
	border: none;
	border-bottom: 2px solid #aaa;
	background-color: #eee;
}
div.contacto h1 + div textarea {
	width: 100%;
	min-height: 100px;
	margin-top: 7px;
	margin-bottom: 20px;
	border: 2px solid transparent;
	-o-border-image: linear-gradient(to right bottom, #aaa, #eee);
	border-image: linear-gradient(to right bottom, #aaa, #eee);
	border-image-slice: 1;
	outline: none;
}
div.contacto h1 + div button {
	margin: auto;
	display: block;
	padding: 15px;
	border: 2px solid transparent;
	-o-border-image: linear-gradient(to right bottom, #aaa, #eee);
	border-image: linear-gradient(to right bottom, #aaa, #eee);
	border-image-slice: 1;
	border-radius: 7px;
	box-shadow: 0px 0px 10px #aaa;
}

div.contacto .errorEnFormulario {
	color: #f00;
	text-align: center;
}
