div.alta h1,
div.alta h2 {
	text-align: center;
	text-shadow: 0px 0px 5px #eee;
	line-height: 5rem;
}

div.alta .alta-form {
	width: 50%;
	min-width: 300px;
	min-height: 60vh;
	background-color: #eee;
	margin: auto;
	border-radius: 7px;
	box-shadow: 0 0 10px #222;
	padding: 20px;
}
div.alta .alta-form label {
	line-height: 2rem;
	border-radius: 5px;
	padding: 0 15px;
	font-weight: bold;
}
div.alta .alta-form label::first-letter {
	text-transform: capitalize;
}
div.alta .alta-form input {
	display: block;
	width: 100%;
	margin-top: 7px;
	margin-bottom: 20px;
	line-height: 2rem;
	outline: none;
	border: none;
	border-bottom: 2px solid #aaa;
	background-color: #eee;
}
div.alta .alta-form button {
	margin: auto;
	display: block;
	padding: 15px;
	border: 2px solid transparent;
	-o-border-image: linear-gradient(to right bottom, #333, #eee);
	border-image: linear-gradient(to right bottom, #333, #eee);
	border-image-slice: 1;
	border-radius: 7px;
	box-shadow: 0px 0px 10px #aaa;
}
div.alta .alta-form #envio {
	display: inline-block;
	width: -moz-min-content;
	width: min-content;
}

div.alta th {
	text-transform: uppercase;
	background-color: #111;
	color: #eee;
	border: 2px solid #222;
	border-bottom: 2px solid #aaa;
	line-height: 2rem;
	padding: 5px;
}

div.alta table {
	border-collapse: collapse;
	margin: 5px auto;
	border: 2px solid #aaa;
	background-color: #eee;
	text-align: center;
	width: 90%;
}

div.alta td {
	border: 1px solid #aaa;
	padding: 5px;
}

div.alta .errorEnFormulario {
	color: #f00;
	text-align: center;
}

@media screen and (max-width: 1100px) {
	div.alta .table {
		max-width: 87%;
		overflow-x: scroll;
		margin: auto;
	}
}
