div.carrito {
	min-width: 100%;
	position: absolute;
	right: 0;
	border: 1px solid #aaa;
	min-height: 96vh;
}

div.carrito h2 {
	border-bottom: 1px solid #aaa;
	margin: 0;
	padding: 0;
	display: block;
	height: 6rem;
	line-height: 6rem;
}
.carrito .productos {
	overflow: scroll;
	max-height: 41vh;
	scrollbar-width: none;
}
.carito .productos::-webkit-scrollbar {
	display: none;
}

div.carrito .producto {
	height: 100px;
	width: 100%;
	display: grid;
	grid-template-columns: 0.3fr 1fr 1fr;
	position: relative;
	margin-bottom: 10px;
	border-bottom: 1px solid #ddd;
}

div.carrito .producto img {
	height: 100px;
	grid-row: 1/2;
	grid-column: 1/2;
	width: 100px;
}

div.carrito .producto button img {
	width: 20px;
	height: 10px;
}

div.carrito .producto p:first-of-type {
	grid-column: 2/4;
	position: absolute;
	top: 0px;
	left: 0;
	height: 2rem;
}
div.carrito .producto p {
	grid-column: 3;
	position: absolute;
	bottom: 0;
	margin: 3px 0;
	right: 0;
}
div.carrito .producto div.cantidad {
	grid-column: 2;
	position: absolute;
	bottom: 0;
	width: 150px;
	border: 1px solid #333;
	border-radius: 30px;
	padding: 0 8px;
	height: 2.5rem;
	line-height: 2.5rem;
}
div.carrito .producto div.cantidad button:first-of-type {
	float: left;
}

div.carrito .producto div.cantidad button {
	border-radius: 50%;
	/* margin: 0 5px; */
	float: right;
	border: none;
	font-size: 1.5rem;
	padding: 0;
}

div.carrito .producto div.cantidad button:hover {
	cursor: pointer;
}

div.carrito .producto div.cantidad p {
	display: inline-block;
	width: 40px;
	margin: auto;
	text-align: center;
}
div.carrito .producto .botar {
	grid-column: 3;
	position: absolute;
	top: 0;
	right: 0;
	margin: 4px;
}

.carrito .cuenta span {
	float: right;
}
.carrito .cuenta p {
	border-bottom: 1px solid #bbb;
}

.carrito .cuenta p:last-of-type {
	font-size: 1.5rem;
	font-weight: bold;
	border-bottom: none;
}

.carrito .cuenta p:nth-child(3) {
	color: #f00;
}

.carrito .conjunto {
	position: absolute;
	bottom: 0;
	width: 100%;
	background-color: #fff;
}

.carrito .conjunto .botones {
	display: flex;
	justify-content: space-between;
	padding: 10px;
	color: #fff;
}

.carrito .conjunto .botones button {
	padding: 10px;
	width: 30%;
	border: none;
	box-shadow: 1px 1px 1px 1px #333;
}

.carrito .conjunto .botones button:first-of-type {
	background-color: #0f0;
}

.carrito .conjunto .botones button:last-of-type {
	background-color: #f00;
}

.carrito .conjunto .botones button img {
	width: 30px;
}
